body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header-base {
  position: fixed;
  z-index: 10000;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 70px;
  background-color: rgb(27,27,27);
  border-bottom: thin solid #000000; 
  border-width: 2px;
}

.header-animation {
	transition: 1s ease;
	background-color: white;
}

.header-animation-back {
	transition: 1s ease;
	background-color: rgb(27,27,27);
}

.header-default {
  height: 100%;
  display: flex;
	flex-direction: row;
	justify-content: flex-end;
	align-items: center;
}

.header-placeholder {
  width: 100%;
  height: 70px;
}

.header-default-icon {
  fill: white;
  position: absolute;
  top: 10px;
  left: 10px;
  width: 65px;
  height: 46.2px;
	border-radius: 2px;
}
.header-default-icon-animation {
	transition: 1s ease;
  fill: rgb(27,27,27);
}

.header-default-icon-animation-back {
  fill: white;
	transition: 1s ease;
}

.normal-button {
  border: none;
  display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: center;
  padding:10px;
  margin: 10px;
	background-color: rgb(27,27,27);
  border-style: none;
  border-color: white;
}
.normal-button-animation {
  transition: 1s ease;
  background-color: white;
  border-color: rgb(27,27,27);
  
  /* fill: black; */
}

.normal-button-animation-back {
	transition: 1s ease;
  background-color: rgb(27,27,27);
  border-color: white;
}

.normal-button-image {
  width: 20px;
  height: 20px;
  fill: white;
}
.normal-button-image-animation {
  transition: 1s ease;
  fill: rgb(27,27,27);
}

.normal-button-image-animation-back {
  transition: 1s ease;
  fill: white;
}

.normal-button-title {
  background-color: rgb(27,27,27);
  margin-left: 10px;
  color: white;
  font-weight: bold;
}
.normal-button-title-animation {
  transition: 1s ease;
  background-color: white;
  color: rgb(27,27,27);
}
.normal-button-title-animation-back {
  transition: 1s ease;
  background-color: rgb(27,27,27);
  color: white;
}
.normal-button:hover {
  border-bottom: thin solid #000000; 
  border-radius: 2px;
}

.about-card {
  display: flex;
  /* position: relative; */
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 10%;
}

.about-detail {
  width: 300px;
  height: 90px;
  margin-bottom: 20px;
  padding: 20px;
  border: thin solid #000000; 
  border-color: rgb(27,27,27);
  border-width: 2px;
  border-radius: 2px;
  position: relative;
}

.about-square {
  position: absolute;
  top: 50%;
  left: -50px;
  margin-top: -10px;
  width: 20px;
  height: 20px;
  background-color: rgb(27,27,27);
}

.about-item {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 500;
  margin-bottom: 30px;
}

.about-img {
  border-radius: 5px;
  width: 200px;
  height: 300px;
  /* margin-bottom: 50px; */
}

.about-img-right {
  /* position: relative; */
  right: 100px;
  margin-left: -50px;
}
.about-img-left {
  /* position: relative; */
  left: 100px;
}

.about-text {
  z-index: 500;
  text-shadow: 0 0 2px #fff; /* horizontal-offset vertical-offset 'blur' colour */
  -moz-text-shadow: 0 0 2px #fff;
  -webkit-text-shadow: 0 0 2px #fff;
  margin-left: -50px;
  margin-top: -100px;
}

